"shop_name": "LiquidCourage",
"user_id": 10127804,
"creation_tsz": 1275331311,
"title": "Custom Flasks, Liquor Bottle Labels, & Bottle Openers",
"announcement": "We are a husband and wife team that left our corporate cubicles and started our business on Etsy in 2010.  We print our flask and liquor bottle label designs on high quality vinyl. Personalize any flask in our shop with your name, initials, or picture. Convo us or call us at (888) 959-2870 with any questions.\n\nBe sure to sign up for our newsletter to stay up to date with our latest designs, discounts, and giveaways!\n\nNEWSLETTER SIGNUP LINK:\nhttp://eepurl.com/FP_fT",
"currency_code": "USD",
"is_vacation": false,
"vacation_message": "Thank you for visiting Liquid Courage!  We specialize in flasks to match every personality.  All flasks are now available in your choice if 8 oz, 6 oz, or 4 oz sizes for the same low price.  Don&#39;t see what you are looking for, have your own idea or image, or want multiples of certain designs?  Convo us for a custom listing.",
"sale_message": "Thank you for your purchase from Liquid Courage Flasks!  Be sure to sign up for our newsletter to stay up to date with our latest designs, discounts, and giveaways!\r\n\r\nNEWSLETTER SIGNUP LINK:\r\nhttp://eepurl.com/FP_fT\r\n\r\n-Shipping-\r\nOur items typically ship within 2 business days and have an estimated delivery time of 2-5 business days within the United States.  If you purchased a custom item, a convo will be sent to you shortly to request more information or to approve a mock up.  Please let us know if you have any questions.  \r\n\r\n-International Shipping-\r\nInternational shipments have an estimated delivery time of 6-10 business days from the time of shipment.  Please note that the shipping time may vary due to customs policies in your country.  International customers are responsible for all Destination Duties, Taxes, and Import Charges.\r\n\r\nIf you have any issues with your order at all.  Please be sure to reach out before leaving feedback.  We will do everything within our power to correct the situation.\r\n\r\nThanks!\r\nAmy and Tyler Fisk\r\nLiquid Courage\r\n888-959-2870\r\nsales@liquidcourageflasks.com",
"digital_sale_message": null,
"last_updated_tsz": 1460682859,
"listing_active_count": 617,
"digital_listing_count": 0,
"login_name": "LiquidCourage",
"accepts_custom_requests": true,
"policy_welcome": "Hello and welcome to our shop! My name is Amy, and my husband, Tyler and I are the owners and creators of Liquid Courage. We have been selling online since 2003. Recently, we decided to make the leap from selling items made by others to selling items that we make. We have been goofing around making all sorts of arts, crafts, scrapbooks, and clothing for many years and decided to combine my hobby with my online business experience. \r\n\r\nTyler has an Accounting degree from Tennessee Technological University, and I have a bachelor&#39;s degree in Marketing and an MBA in Accounting from TTU as well.  Upon graduating, we tried &quot;real, grown-up jobs&quot; - Tyler as a general manager at a greenhouse manufacturing company, and I as a staff accountant for a nationwide manufacturing company.  We spent a couple of years working for &quot;the man&quot;...at a desk eight hours a day...fifty weeks a year.\r\n\r\nWe finally decided to follow our dream of becoming entrepreneurs, pursue our passion, and determine our own future with hands on approach. We chose the path less traveled by, and that has made all the difference.\r\n\r\nPlease don&#39;t hesitate to ask if you have any questions or would like a custom item.\r\n\r\n(888) 959-2870 (M-F 9am - 5pm Eastern)\r\nor\r\nConvo us anytime! \r\n\r\nCheers! \r\nTyler and Amy",
"policy_payment": "SECURE OPTIONS\nEtsy keeps your payment information secure. Sellers don't receive your credit card information.\n- Visa\n- MasterCard\n- American Express\n- Discover\n- PayPal\n- Apple Pay\n- SOFORT Bank Transfer\n- iDEAL\n- Etsy Gift Card",
"policy_shipping": "PROCESSING TIME\nThe time I need to prepare an order for shipping varies. For details, see individual items.\n\nESTIMATED SHIPPING TIME\nNorth America: 2 - 5 business days\n\nI'll do my best to meet these shipping estimates, but cannot guarantee them. Actual delivery time will depend on the shipping method you choose.\n\nCUSTOMS AND DUTIES FEES\nBuyers are responsible for any customs or duties fees that may apply. Sellers aren't responsible for delays due to customs",
"policy_refunds": "I GLADLY ACCEPT RETURNS AND EXCHANGES\nJust contact me within: 7 days of delivery\nShip returns back to me within: 14 days of delivery\n\nI DON'T ACCEPT CANCELLATIONS\nBut please contact me if you have any problems with your order.\n\nTHE FOLLOWING ITEMS CAN'T BE RETURNED\nBecause of the nature of these items, unless they arrive damaged or defective, I can't accept returns for:\n- Custom or personalized orders\n\nCONDITIONS OF RETURN\nBuyers are responsible for return shipping costs.\nIf the item is not returned in its original condition, the buyer is responsible for any loss in value.",
"policy_additional": "I will only use your shipping and billing address, and contact information\n- To communicate with you about your order\n- To fulfill your order\n- For legal reasons (like paying taxes)\n- Email newsletter\n\nHOW MANY BOTTLES AND/OR LABELS DO I GET FOR $25?\nPlease see on the right hand side of the listing, there is a drop down menu called &quot;Label/Bottle Options&quot; with different quantities and pricing. Don&#39;t see a quantity that will work for you? That&#39;s not a problem. Please just contact me, and I will add a custom quantity for you.\n\nWHAT SIZE ARE THE BOTTLES?\nThe 50mL plastic bottles are approximately 4 inches tall.\n\nCAN I CHANGE THE WORDING/COLORS OF THE LABEL DESIGN?\nYes, we can change the wording any way that you would like. Please leave a note to seller at checkout with your preferences, and I will create a mock up for you to proof.\n\nWILL I GET TO SEE A PROOF OF THE DESIGN BEFORE THE LABELS ARE PRINTED?\nYes, we will send you a mock up to proof before the labels are printed and shipped.\n\nHOW DO I SEND YOU THE PERSONALIZATION THAT I WANT?\nAt checkout, please send us a note to seller with your custom preferences.\n\nSHIPPING/TURNAROUND TIME\nWe ship within 2 business days of the approval of the mock up. Shipping takes 2-5 business days. Upgraded/Overnight shipping is available upon request for an additional fee.",
"policy_seller_info": "",
"policy_updated_tsz": 1372190570,
"policy_has_private_receipt_info": false,
"vacation_autoreply": "Thank you for visiting Liquid Courage!  We specialize in flasks to match every personality.  All flasks are now available in your choice if 8 oz, 6 oz, or 4 oz sizes for the same low price.  Don&#39;t see what you are looking for, have your own idea or image, or want multiples of certain designs?  Convo us for a custom listing.",
