"title": "Bridesmaid Gift, Personalized Bridal Party Gift, Engraved Hip Flask, Personalized Whiskey Flask, Pocket Size Flask",
"description": "The perfect sized hip flasks will fit in a small clutch or handbag-a perfect gift for your bridesmaid. Designed to carry liquor, our smaller, 5 oz. black flasks are made of stainless steel. Our state-of-the-art laser engraves the black coating, resulting in a durable, beautiful silver engraving.  \n\nOur listing is  ONE personalized flasks, and the above engraving designs. \n\nFlask Gift Box Sets include a black gift box with engraved flask and a stainless steel funnel only, they do not include a cup.\n\nSPECIFICATIONS:\n3-1/2&quot; wide x 4&quot; tall;\nHolds 6 ounces of liquor;\nFood grade stainless steel;\nSilver stainless steel cup holds 1 ounce of liquor.\n\nSPECIFICATIONS for gift box upgrade\nGift Box is 5-1/2&quot; wide, 6-3/4&quot; tall;\nIncludes flask \nStainless Steel Funnel \n\nPERSONALIZATION:\nWe will laser-engrave the front side of each flask with your personalized information. \n\nORDERING:\nSelect the quantity from the drop-down menu, click the green &#39;add to cart&#39; button, and specify all engraving information in the note box on the add to cart page, including the following information if applicable:\n1) Name;\n2) Title;\n3) Wedding date.\n\nMost orders are ready and shipped within 3 to 5 business days after receiving payment.\n\nINTERNATIONAL ORDERS are WELCOME!\n\nThank you for visiting our shop!\n* * * * * * * * * * * * * * * * * * * * *",
