"title": "Personalized & Sophisticated Gifts",
"announcement": "Personalized Wedding Gifts/Favors; Custom Engraved Beer Mugs, Swing Top Glass Flasks, Beer Can Glasses, Wine Glasses; FREE LASER ENGRAVING; Custom Orders Welcomed; Laser Engraved Bamboo Cutting Boards, Bottle Openers, Lighters, Stainless Steel Flasks, Leatherette Portfolios and Business Card Holders.  Personalized Gifts for Bride and Groom, Bridesmaids, Groomsmen, Best Man, Maid of Honor, Matron of Honor, Ushers, Ring Bearers, Mother and Father of the Bride, Mother and Father of the Groom, and many more!",
"currency_code": "USD",
