"vacation_message": null,
"sale_message": "Thank you for purchasing from Queen Bee Concepts.  Your business is greatly appreciated! If you have any questions or comments feel free to contact us.  We will respond to you promptly.\r\n\r\nFollow us on Facebook for Additional Information and Updates on New Designs",
"digital_sale_message": null,
"last_updated_tsz": 1460682372,
"listing_active_count": 37,
"digital_listing_count": 0,
"login_name": "kaciboatwright",
"accepts_custom_requests": true,
"policy_welcome": "Thank you for visiting Queen Bee Concepts.  We offer a beautiful array of personalized and sophisticated gifts for weddings and other occasions.  Weddings are a special time in a couples lives and we want our products to represent that love with a timeless splendor.\r\n\r\n**Follow us on Facebook for additional information on our products, behind the scenes info and news on upcoming designs and products.",
"policy_payment": "We accept Visa, MasterCard, Discover via Paypal or direct checkout.\r\n\r\n***If you need to cancel your order, you must do it as soon as possible.  A full refund is available for orders as long as we have not begun work on your order.  Due to the customized nature of our orders, once we start on an order no refund will be issued***",
"policy_shipping": "Upon completion of your order, a USPS shipping confirmation will be provided along with a tracking number.  Insurance is included with our shipping and handling.  1-2 day rush processing is available in our shop with expedited priority mail shipping for an extra $45.\r\n\r\nAt times, due to timing and cost comparison, UPS will be used as an alternate form of shipping.  As with USPS, when we utilize UPS, you will be provided a tracking number at the time your order is ready to ship.\r\n\r\n*Please note: Queen Bee Concepts is not responsible for USPS or UPS delay or damage.\r\n\r\nFor most items we ship USPS Priority Mail.  The time allotted for this particular shipping is 1-3 days from receipt of shipping confirmation (based on information from USPS.com). ",
"policy_refunds": "Refunds:\r\nAs long as an order has not been completed, a refund is available.  However, if an order has been started or is completed all sales are final and no refunds or exchanges will be made for any reason.\r\n\r\nDamaged Items:\r\nPlease inform us of any damage within 48 hours of receipt of your order.  Claims can be emailed along with a picture of the damage to qualify for our free replacement guarantee.\r\n\r\nSpelling Errors:\r\nRefunds will be given on misspelled items if it is due to our mistake.  To qualify for free replacements please send us a photograph of the personalization and we will mail a new item within 72 hours.",
"policy_additional": "We welcome custom orders.\r\n\r\nFor any questions regarding our products or designs please send us a message.  We will get back to you in a timely matter.\r\n\r\n***Due to item availability and time needed to complete certain orders, some products such as glasses and mugs, may differ from the picture shown.  However, as always, we will do our very best to substitute with items that match our pictures***",
"policy_seller_info": "Queen Bee Concepts\r\n1929 Northside Drive East\r\nStatesboro, GA 30458\r\nqueenbeeconcepts@yahoo.com\r\n\r\n***Follow us on Facebook for updates on new designs, behind the scenes information, and much more!***",
