"title": "Engraved Scotch Glasses Personalized Whiskey Decanter Set Groomsmen Gift Set",
"description": "## Please include Phone Number (for shipping) in Order Note ##\n\nThis listing is for 1 set whiskey decanter & glasses.\nThe set includs 1 decanter and 2 glasses.\n\n--- Get Discount & Combined Shipping ---\n\nBuy 5 Sets Get 5% off\nhttp://etsy.me/1CSoP7q\n\nBuy 6 Sets Get 6% off\nhttp://etsy.me/1HEEN44\n\nBuy 7 Sets Get 7% off\nhttp://etsy.me/1SxlRr2\n\nBuy 8 Sets Get 8% off\nhttp://etsy.me/1SxlUmJ\n\nBuy 9 Sets Get 9% off\nhttp://etsy.me/1IeZdUI\n\nBuy 10 Sets Get 10% off\nhttp://etsy.me/1RJGfKp\n\nBuy 11 Sets Get 11% off\nhttp://etsy.me/1JswB51\n\nBuy 12 Sets Get 12% off\nhttp://etsy.me/1Dqo1lf\n\nLooking for perfect groomsmen gift? What says more about your good taste than an engraved decanter? Crafted from fine European lead-free glass it makes a classic style statement in your own home, or a distinctive present for groomsmen or just him.\n\nEach decanter & glass is customized to your requirements. Suggested engravings includes names / dates / monogrammed initials / quotes / custom logo patterns. Free of charge for all engravings!\n\nDimensions\nDecanter, 9&quot;H x 3-1/2&quot;W, 32 oz.\nGlass, 3&quot;H x 3&quot;W, 8 oz.\n\nPersonalize instruction\nPlease comment your custom requirements as detailed as possible in the Note to Seller section upon check out. Or send me conversation right after you placing the order. (IMPORTANT! Pls also leave your phone number for shipping.)\n\nHandling & Shipping\nWe need 10-15 business days to process your order\nShipping usually takes 10 days \nWe suggest you placing the order 25-30 days before the event.\n\nThanks for stopping!",
