"title": "Classico Gold Rim Custom Whiskey Glasses, Set of 4",
"description": "Some believe that the key to good whiskey is the glass you drink it from, and we completely agree. Give your whiskey the glass it deserves with our custom gold rim whiskey glasses.  Whiskey glasses measure 4” x 3” and can hold 10 ounces of his preferred spirits. Hand washing is recommended to keep these glasses looking like new.\n\n-Made from premium glass with 22 kt gold rim details\n-Glasses are engraved with the monogram of your choice\n-Hand washing is recommended\n-Set of four, each measuring 4&quot; x 3&quot; with a 10 oz capacity\n\n:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::\nWHEN WILL MY PERSONALIZED ITEM SHIP:\n:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::\nAll items are stocked and personalized one at a time in Oklahoma City, OK. After your order is placed, it typically takes 1-2 business days to ship out (3 days during holidays). Depending on where you live, it then typically takes an additional 4-5 days for the item to arrive. (please allow an extra 4-5 days for Alaska, Hawaii, and Puerto Rico)\n\n\n:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::\nHOW TO ORDER PERSONALIZED ITEMS:\n:::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::::\nA) Please select your item and add it to the cart\nB) Please INCLUDE IN THE CHECKOUT NOTES:\n1) Three Initials for Monogram (Family name initial is typically in the center. For example, John F. Kennedy would be JKF.)\n\n*** Note: The item will be personalized engraved in the font shown. The more text you put the smaller the engraving will have to be to fit the object. Flasks are limited to 2-3 lines, all other items are limited to the personalization areas shown. We will make all efforts to fit your personalization, but please keep it reasonable, no special characters or emoticons please. As a general rule, if it&#39;s not on your keyboard, we cannot print it. Enter your personalization EXACTLY as it should to engraved. We will engrave letters in the same order you list them in. ***\n\nAll artwork, designs, and photos Copyright © HomeWetBar.com",
