"policy_welcome": "Keep it affordable. ",
"policy_payment": "Paypal only.\r\n\r\n",
"policy_shipping": "FOR INTERNATIONAL DELIVERIES, WE ARE NOT RESPONSIBLE FOR DUTIES AND TAXES COLLECTED BY YOUR COUNTRY&#39;S CUSTOM OFFICE. THESE ARE NOT INCLUDED IN YOUR SHIPPING COST. \r\n\r\nWE DO NOT GUARANTEE DELIVERIES BY ANY DEADLINE. ALL OF OUR PRODUCTS ARE MADE-TO-ORDER AND PERSONALIZED. SHIPPING TIME IS ESTIMATED ONLY. GENERALLY SPEAKING, WE NEED  7 BUSINESS DAYS. ",
"policy_refunds": "No refunds on customized items; however if you get the item damaged to a point that it cannot be used, we will redo and reship at our cost. If the item is damaged, e.g. scratched, but still can be used, we&#39;ll come up with a reasonable settlement.\r\n\r\n",
"policy_additional": "Owing to the sudden growth of our international business (thank you Etsy!), the brother in our team has opened a very, very little work shop in Hong Kong. This effort not only moves us closer to some of our suppliers, but also allows us to ship international orders to Australia, Singapore, Japan, etc. including much of Europe, South America... cheaper and faster. \r\n\r\nNorth American customers, mainly USA and Canada, will not be affected by this addition, for you good folks, it&#39;s business as usual: engraved and shipped from Houston, Texas. Yee Haw!\r\n\r\nThis concept looked good on paper, let&#39;s hope it pays off- wish us luck!\r\n\r\nLastly, for items with multiple colors, for example journals, since our suppliers cannot keep accurate stock levels, we may substitute your purchase with the same product in a different stocked color. \r\n  \r\nImportant Notes:\r\n\r\n*Expedited orders, large orders or orders with not stock in the USA will come from our Hong Kong shop. We reserve the right to ship from either shops. \r\n\r\n*Ordering samples before a large order is prudent; however owing to the nature of our business, we cannot guarantee a sample and the final production will look &quot;exactly&quot; alike as we have different machinist and many machines that all engrave / print slightly different. If getting &quot;exact&quot; is critical, please let us know at order so we can find the previous order documents and make sure we use the same machinist to engrave the order. We may request a visual photo of the sample for a final reference.   \r\n",
