"announcement": "Personalized Whiskey Barrels, Custom Barrels, Quarter Barrel Signs, Barrel Heads, Wine Coasters and Serving Trays.  WE SHIP WORLDWIDE! International orders  & P.O. Box&#39;s all require a valid phone number! ✪✪✪✪✪✪✪✪✪✪✪✪✪VIEW CURRENT PRODUCTION TIMES & SHIPPING INFO ✪✪✪✪✪✪✪✪✪✪✪✪ ✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪✪\n\n✦✦✦✦✦✦CURRENT PRODUCTION TIMES:✦✦✦✦✦✦\nWe update all production times and stock notices here since it is not feasible to edit our hundreds of listings just for the Holidays, temporary backorders and other peak times.\nBarrels: 5-7 business days \nWedding Card Barrels: 5-7 business days\nSigns (Printed): 5-7 business days\nSigns (Engraved): 5-7 business days\nTrays: 5-7 business days\n*If you placed a full custom order that includes custom art we are creating, please allow additional time for art creation, proofing, etc. and be sure to inquire on current proofing times. We only include proofing on orders containing custom artwork and not on any other type of order to reduce proofing times. For all full custom orders, production time starts after proof is approved.\n\n✦STOCK NOTICE:✦\n*Please be sure to read our substitution policies for all OOS hoop colors if ordering near a Holiday.\n\n********************************************************************************************\n✪✪✪✪✪✪✪✪NEED IT FAST? ADD RUSH OPTIONS✪✪✪✪✪✪✪✪\n********************************************************************************************\nRUSH PRODUCTION: https://www.etsy.com/listing/172997767/production-rush-get-your-barrels-goods?ref=shop_home_active_1&ga_search_query=rush This option gets your order produced faster, usually within 2-3 business days.\n\nRUSH SHIPPING: All shipping is now automatically calculated by Etsy based on weight, size and shipping destination. No need to contact us for a quote, simply select what option you want during checkout.\n\n********************************************************************************************STANDARD TRANSIT TIMES BASED ON SHIPPING METHOD AND DESTINATION:\n********************************************************************************************\nCONTINENTAL USA (excludes AK, HI, PR or international destinations)\nFREE SHIPPING: 6-8 Business days-FedEx Smart Post, UPS Sure Post, Parcel Select\nPRIORITY: 2-3 business days transit\nPRIORITY EXPRESS: 1-2 Business days transit\n\nAK, HI & PR:\nFREE SHIPPING: Not offered\nPRIORITY: 3-5 Business days average\nPRIORITY EXPRESS: 2-3 business days average\n\nINTERNATIONAL DESTINATIONS:\nFREE SHIPPING: Not offered\nUSPS 1ST CLASS: 2 weeks\nPRIORITY: 6-10 Business days average\nPRIORITY EXPRESS: 3-5 business days average\n\nNOTE: Average transit times are not meant to guarantee delivery dates and are estimated transit times for many major markets. Actual number of days may vary based on origin, destination, and if internationally shipping-customs delays. We can in no way guarantee specific delivery dates with any order that must be processed through customs. \n\n✦Daily Cutoff Times:\nOrders submitted after 11 am EST may be processed on the next business day.\n\nPlease view our policy page here for FAQ&#39;s and other important information:\nhttps://www.etsy.com/shop/BootlegBarrels/policy?ref=shopinfo_policies_leftnav\n\nWe thank you for your business and hope you love your barrels :)",
