"description": "These personalized quintessential whiskey stones will make a stylish luxurious present for a wedding, birthday, or groomsmen party. Cubes with engraved logos are wonderful corporate gifts for your VIP business partners. The whiskey stones can serve as an addition to the main gift-a bottle of good whiskey- or be presented on its own.\n\nNOTE: See all designs on the last picture.\n\nComes in an individual black velvet bag.\n\nSize: 1&quot;\n\n&quot;HOW TO ORDER&quot; \n1. Use the drop down menu on the right to select the quantity and design.\n2. Select “Add to Cart.”\n3. Please indicate what you would like to appear on the cubes using the &#39;NOTES&#39; section. The information you enter will be engraved exactly as you write it out.\n4. Enter a valid shipping address for the completion of the order. If you are sending the board directly to the gift recipient, please submit their address instead. If you are sending items to several recipients please place separate orders, as per address.\n5. Please look over the text you wish to have engraved for spelling errors! Feel free to contact us with any questions or concerns you may have.\n\n&quot;PERSONALIZATION&quot;\nPlease indicate: \n(Example) GMH or AD or W\n\n&quot;LEAD TIME&quot;\nWe will process and ship your order withing 3-7 business days that it comes in.\nWe also offer RUSH ORDER (1 day turnaround) for an additional charge, so please go to RUSH ORDER service: https://www.etsy.com/listing/212033089/rush-order-option-fast-turnaround-24?ref=shop_home_active_17",
