"title": "Cake Toppers, Personalized Cutting Boards, Wedding Gift",
"announcement": "Cake Topper, Cutting Board, Wedding Gifts, Personalized Gifts, Groomsmen Gifts, Bridesmaid Gifts, Ring Bearer Gifts, Birthday Gift, Anniversary Gift, Graduation Gift, Corporate Gift, Personalized Beer Glass, Custom Beer Glass, Custom Wine Glass, Personalized Wine Glass, Wine Glass, Beer Glass, Pilsner Beer Glass, Personalized Hanger, Custom Hanger, Monogram, Baseball, Mini Bat, Pocket Knife, Personalized Pocket Knife, Engraved Knife, Custom Pocket Knife, Flask, Personalized Flask, Custom Flask, Groomsmen Flask, Bridesmaid Flask, Pink Flask, Personalized Wood Pen, Custom Mirror, Glass Sign, Bowling Pins, Wedding Party Gift, Christmas Gift, Holiday Gift, New Year Gift, Hanukkah Gift, Valentine Day Gift, ",
"currency_code": "USD",
"is_vacation": false,
"vacation_message": null,
"sale_message": "Thank you very much for shopping at MW Gift Shop. We deeply appreciate your recent purchase. Please be assured that we&#39;ll do everything we can to make your  shopping experience at our store fantastic and memorable. Your satisfaction and feedback are very important to us because we value each and every client and make sure you get what you pay for and even more. At MW Gift Shop we truly care about you and are always open to the two-way conversation to ensure your satisfaction with our product.",
"digital_sale_message": null,
"last_updated_tsz": 1460681057,
"listing_active_count": 290,
"digital_listing_count": 0,
"login_name": "mwgiftshop",
"accepts_custom_requests": true,
"policy_welcome": "MW Gift Shop is owned by Magic Wood Shop and specializes mostly in stylish, tasteful and elegant Personalized Laser Engraved Cutting Boards and Custom Made Cake Toppers. Other personalized laser engraved gifts are also available to make any occasion one of a kind. We carry a large assortment of wedding gifts including Personalized Flasks, Monogrammed Beer and Wine Glasses, Customized Hangers, and much more. Wide selection of Personalized Laser Engraved Gifts is also available for Holidays and Theme Parties.\r\nWe are open to your suggestions and ideas and work hard to make your vision of the unique gift design come true.",
"policy_payment": "We accept all major credit and debit cards; Visa, Master Card, American Express, Discovery.\n\nAlso we accept PayPal.\n\nNew York Residents will be charged States Sales Tax in amount of 8.875% upon checkout.",
"policy_shipping": "TURNAROUND\r\nWe are working hard to have your order shipped out as soon as possible. Please note that it will not always be on the same day the order was placed. Depending on our work load, the processing time may vary from 1 to 10 business days. Feel free to contact us if you would like to find out the lead time for a specific item you are interested in. \r\n\r\nDOMESTIC SHIPPING\r\nWe use USPS First Class, USPS Priority mail, and FedEx Ground shipping destinations within the US. The amount of time it takes to receive the delivery depends on the carrier. Unfortunately, once the order leaves our workshop, we have no control over their accuracy or delays in shipping. You can find the estimated time for delivery on the carrier website.\r\n\r\nPlease make sure to you specified correct shipping address before completing check out as this is the exact address we will ship your order to.\r\n\r\nINTERNATIONAL SHIPPING\r\nAll international orders are shipped via USPS First Class Mail. Please note that you won’t be able to track your package once it leaves the U.S. using USPS tracking number provided. Delivery time varies by destination and depends on foreign Customs processing time. We are not responsible for lost, stolen, or damaged during the shipment items. Please be advised that all packages are insured. If you received damaged item, please contact us for further instructions on how to receive your compensation.\r\n \r\nCUSTOMS CHARGES\r\nIt is the customers responsibility to contact the local customs, the courier office regarding any additional and applicable customs, duties, or Vat charges.\r\n \r\nIf you have any questions or concerns, please do not hesitate to contact us.",
"policy_refunds": "REFUNDS AND EXCHANGES\r\nAll personalized item sales are final and  not subjects to refund or exchange. Please make sure that you provided us all order details and correctly entered all information before completing your purchase. Please be advised that all packages are insured. If you received damaged item, please contact us for further instructions on how to receive your compensation.\r\nNOTE: Please remember to doublecheck your spelling as the wording will appear on the item exactly as you wrote it out when placing the order. We won&#39;t be responsible for incorrect spelling unless due to our negligence. The customer will have to pay again if he/she wants us to make another item with correct spelling.\r\n\r\nORDER CANCELLATION\r\nPLEASE NOTE WE WILL CANCEL YOUR ORDER IF YOU HAVEN&#39;T SUBMITTED DESCRIPTION AND FAILED TO RESPOND TO OUR MESSAGE REGARDING YOUR ORDER DESCRIPTION.",
